//artsits
.artists {
	&__inner {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between; }
	&__item {
		width: calc(25% - 16px);
		margin-bottom: 24px;
		display: flex;
		@include sm {
			width: 100%; }
		&_lg {
			width: calc(50% - 10px); }
		&_double {
			flex-wrap: wrap;
			& .artist:not(:last-child) {
				margin-bottom: 24px; } } } }
.artist {
	width: 100%;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	position: relative;
	padding-bottom: 516 / 680 * 100%;
	&:hover {
			.artist__content {
				opacity: 1; } }
	&__name {
		font-size: 18px;
		color: #ffffff;
		margin-bottom: 15px; }
	&__btn {
		.btn {
			text-decoration: none;
			color: #ffffff;
			padding: 10px;
			border: 1px solid #fff; } }
	// &:before
	// 	position: absolute
	// 	content: ''
	// 	display: block
	// 	height: 100%
	// 	pointer-events: none

	&__content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: absolute;
		left: 0;
		top: 0;
		opacity: 0;
		background-color: rgba(0,0,0,.5);
		width: 100%;
		height: 100%;
		z-index: 1;
		transition: opacity .3s ease; } }



